body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  
}

.container{
text-align: center;
border-radius: 50px;


}
.forma {
  display: flex;
  flex-direction: column;
  width: 40%;
  margin-left: 30%;
  align-items: center;
}
.forma input{
  text-align: center;
  border-radius: 50px;
  background-color: rgb(216, 132, 6);
  font-family: 'montserrat';}
.forma ::placeholder{
  color: white;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body{
  width: 100%;
  background: url(./Ginger\ Honey\ background.png);
  height: auto;
  background-size: cover;
  background-repeat: no-repeat;
  margin: 0px;
  overflow-x: hidden;
  user-zoom: 100%;

  
}


.header{
list-style: none;
display: flex;
flex-direction: row;
width: 100%;
font-family: 'raleway';
font-weight: bold;

}
@media screen and (max-width: 1200px) {
  .header {
    
    margin-top: 20px;
    flex-wrap: wrap;
     
     
   }
 }





#blankspace{
width: 60%;


}
@media screen and (max-width: 1200px) {
  #blankspace {
    width: 0%;
  }
}



#list1 {
width: 10%;
margin-top: 100px;
margin-right: 10%;



}

#list1 a{
  text-decoration: none;
  color: rgb(252, 248, 248);
  border: #CE9C2B;
  border-width: 5px;
  border-style: groove;
  padding: 5px;
  vertical-align: middle;
  font-size: 25px;
}

@media screen and (max-width: 1200px) {
 #list1 {
    position: relative;
    float: left;
    display: block;
    margin: auto;
    z-index: 1;
  }
}

#list2 {
 width: 20%;
 margin-top: 100px;
 margin-left: 3%;
 
 
}

 

#list2 a{
  text-decoration: none;
  color: rgb(247, 241, 241);
}
@media screen and (max-width: 1200px) {
  #list2 {
     position: relative;
     float: right;
     display: block;
     margin: auto;
     z-index: 1;
   }
 }
#list2 span{
  text-decoration: none;
  color: #faf8f4;
  font-size: 40px;
}

#logo1{
  position: absolute;
  margin-left: 5%;
  
}
@media screen and (max-width: 1200px) {
  #logo1 {
      clear: both;
     display: block;
     z-index: 1;
     position: relative;
    
     
    
   }
 }
 @media screen and (max-width: 1200px){
#logo1 img{
width: 80%;
z-index: 1;


}
 }
  
 
#logo2{
  position: absolute;
  margin-left: 20%;
  margin-top: 8%;
  animation-name: animation;
  animation-duration: 1s;
  z-index: 2;
}

 @media screen and (max-width: 1200px) {
  #logo2 img {
     width: 80%;
     margin-left: 45%;
     margin-top: -470px;
     z-index: 2;
     
     
   }
 }
 
@keyframes animation {
0%{margin-left:-200px;}
100%{margin-left: 20%;}


}


#tegla :hover{
  cursor: pointer;
}


#hero1{
  position: absolute;
  opacity: 0;
  width: 20%;
  margin-left: 70%;
  margin-top: 30%;
  cursor: pointer;


}
@media screen and (max-width: 1200px) {
  #hero1 {
     width: 80%;
      margin-top: 30%;
      text-align: center;
      margin-left: 10%;
     z-index: 1;

     
     
   }
 }
#tegla img{
  float: right;
  margin-right: 5%;
width: 700px;
margin-top: 20%;
animation-name: animation1;
animation-duration: 2s;




}
@media screen and (max-width: 1200px) {
  #tegla img {
     position: relative;
     width: 80%;
     z-index: 3;
     
     
   }
 }
@keyframes animation1{
0%{margin-top: -200px;}
100%{margin-top:20%;}

}

#textcontainer{
width: 100%;
text-align: center;
clear: both;
position: absolute;
margin-top: 5%;
color: white;




}

#text1 {
  width: 30%;
  margin-left: 50%;
  font-family: 'raleway';
  font-size: 30px;
  opacity: 0;
}
@media screen and (max-width: 1200px) {
  #text1 {
    
    margin-left: 10%;
    width: 80%;
    margin-top: 20%;

     
     
   }
 }


#gingerhoney {
clear: both;
text-align: center;
color: white;
font-family: 'raleway';



}
.forma{

clear: both;
position: relative;
text-align: center;
color: white;
font-family: 'raleway';
text-transform: capitalize;
font-size: 20px;




}



#emaillabel{
  color: white;
  display: block;


}
#email{
  display: block;
  margin-left: 40%;
  width: 20%;
  border: none;
  border-radius: 40px;
  height: 30px;
  font-size: 20px;
  
}
@media screen and (max-width: 1200px) {
  #email {
    
    margin-left: 20%;
    width: 60%;
     
     
   }
 }
#količina{
  display: block;
  width: 20%;
  margin-left: 40%;
  background-color: #CE9C2B;
  border: none;
  border-radius: 40px;
  height: 30px;
  font-size: 20px;
}
@media screen and (max-width: 1200px) {
  #količina {
    
    margin-left: 20%;
    width: 60%;
     
     
   }
 }
#količinalabel{
  display: block;
}
#submit{
  display: block;
  margin-top: 20px;
  width: 10%;
  margin-left: 45%;
  margin-bottom: 30px;
  background-color: white;
  font-family: 'raleway';
  border: none;
  border-radius: 40px;
  height: 40px;
  text-transform: capitalize;
  font-size: 30px;
  cursor: pointer;

}
@media screen and (max-width: 1200px) {
  #submit{
    
    margin-left: 20%;
    width: 60%;
     
     
   }
 }